<template>
  <div class="login">
    <div class="login-header">
      Hi，欢迎来到XXX
    </div>
    <div>
      <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
      <mt-field label="验证码" placeholder="请输入验证码" v-model="captcha">
        <mt-button :disabled="sending" class="send-btn" size="small" plain type="primary" @click="handleSend">
          {{sending ? `倒计时${count}s` : '获取验证码'}}
        </mt-button>
      </mt-field>
    </div>
    <div class="login-footer">
      <mt-button class="submit-btn" size="small" type="primary" @click="handleLogin">登录</mt-button>
      <div class="footer-tips">登录即表示同意<span>《XXX用户协议》</span></div>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui'
export default {
  name: 'Login',
  data () {
    return {
      captcha: null,
      phone: null,
      sending: false,
      timer: null,
      count: 60,
      username: 'admin'
    }
  },
  created () {
    this.$socket.open()
  },
  methods: {
    handleLogin () {
      this.$socket.login(this.username, '123456', (res) => {
        if (!res.success) {
          localStorage.removeItem('user')
          Toast(res.reason)
        } else {
          localStorage.setItem('user', JSON.stringify(res.user))
          this.$router.push('/chatList')
        }
      })
    },
    handleSend () {
      this.sending = !this.sending
      if (this.sending) {
        this.timer = setInterval(() => {
          if (this.count < 1) {
            this.sending = false
            clearInterval(this.timer)
          } else {
            this.count--
          }
        }, 1000)
      }
    }
  }
}
</script>

<style lang="less">
  .login {
    .login-header {
      height: 160px;
      background: #fff;
      padding-left: 30px;
      text-align: left;
      line-height: 160px;
      font-weight: bold;
      font-size: 18px;
    }
    .send-btn {
      width: 100px;
    }
    .login-footer {
      padding-top: 90px;
    }
  }
</style>
